.imageLinkForm{
	position: relative;
	display: flex;
	flex-direction: column;
	align-content: center;
	width: 80%;
}



.search-bar{
background-color: #026873;
background-image: 
	linear-gradient(90deg, rgba(255,255,255,.07) 50%, transparent 50%),
	linear-gradient(90deg, rgba(255,255,255,.13) 50%, transparent 50%),
	linear-gradient(90deg, transparent 50%, rgba(255,255,255,.17) 50%),
	linear-gradient(90deg, transparent 50%, rgba(255,255,255,.19) 50%);
background-size: 13px, 29px, 37px, 53px;
}

button{
	background: linear-gradient(45deg, #2C3E50 0%, #4CA1AF 100%);
}

.app-discription{
	transition: all 1s ease;
	background: linear-gradient(45deg, #4CA1AF 0%, #2C3E50 100%);
}
.app-discription:hover{
	transition: all 2s ease;
	transform: scale(1.1);
	background: linear-gradient(45deg, #2C3E50 0%, #4CA1AF 100%);

}

.searchHint {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80%;
	height: 3vw;
	box-shadow: 0px 0px 2px 3px #2C3E50;
	border-radius: 1em;
	bottom: -25%;
	left: 10%;
	background: linear-gradient(45deg, #2C3E50 0%, #4CA1AF 100%);
	transition: all 2s ease;
	visibility: hidden;
	opacity: 0;
}

.search-bar:hover .searchHint {
	visibility: visible;
	opacity: 1;
}

.searchHint p{
	font-size: 1.4vw;
	font-weight: 600;
	text-align: center;
	height: 70%;
	
}