.FaceRecognition{
	transform: translateY(3em);
	width: 500px;
}
.bounding-box{
	position: absolute;
	box-shadow: 0 0 0 1px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;


}
.buffer{
	position: relative;
	width: 100%;
	height: 100px;
	z-index: -1;
}